.rights {
  padding-top: 14px;
  grid-area: rights;

  @media (max-width: $tablet-width-max) {
    padding-top: 22px;
  }

  @media (max-width: $mobile-width-max) {
    padding-top: 0;
  }
}

.rights__list {
  @include list-reset;
  display: flex;
  justify-content: space-between;

  @media (max-width: $mobile-width-max) {
    display: block;
  }

}

.rights__item {
  font-family: $font-stack-pt-mono;
  font-size: 13px;
  line-height: 24px;
  font-style: normal;
  font-weight: 400;
  letter-spacing: 0.04em;
  text-align: center;
  color: $secondary-trasporant-50;
  text-transform: uppercase;

  &:last-child {
    margin-left: 7%;
  }

  @media (max-width: $tablet-width-max) {

    &--all-rights,
    &--company {
      display: none;
    }
  }
}
